@use "global" as *;

.p-interior__title {
  text-align: center;
  letter-spacing: myrem(1);
  @include mq(md) {
    margin-left: myrem(21);
    letter-spacing: myrem(3);
  }
}

.p-interior__title::after {
  letter-spacing: myrem(0.5);
  line-height: 1.5;
  @include mq(md) {
    margin-left: myrem(-19);
    letter-spacing: myrem(1.8);
    line-height: 0.6;
  }
}
