@use "global" as *;

.p-news {
  position: relative;
}

.p-news::before {
  content: "";
  display: block;
  position: absolute;
  top: -71%;
  left: 0;
  width: 100%;
  height: 171%;
  background-image: url(../images/bg1.jpg);
  background-size: cover;
  z-index: -10;
  @include mq(md) {
    top: 19%;
    height: 78%;
  }
}

.p-news::after {
  content: "";
  display: block;
  position: absolute;
  top: -71%;
  left: 0;
  width: 100%;
  height: 171%;
  background-color: rgba(245, 240, 247, 0.5);
  z-index: -5;
  @include mq(md) {
    top: 19%;
    height: 78%;
  }
}
.p-news__wrapper {
  margin: 0 auto;
  padding-right: myrem(15);
  padding-bottom: myrem(74);
  padding-left: myrem(15);
  max-width: myrem(520);
  width: 100%;
  @include mq(md) {
    padding-right: myrem(200);
    padding-left: myrem(200);
    max-width: myrem(1400);
  }
}

.p-news__box {
  @include mq(md) {
    display: flex;
    justify-content: end;
    column-gap: myrem(36);
  }
}

.p-news__section-title {
  @include mq(md) {
    width: 30%;
    text-align: end;
  }
}

.p-news__section-title::after {
  line-height: 0.7;
  @include mq(md) {
    padding-right: 30%;
    letter-spacing: myrem(1);
  }
}

.p-news__items {
  padding-top: myrem(25);
  @include mq(md) {
    padding-top: myrem(18);
    max-width: myrem(623);
    width: 70%;
  }
}

.p-news__item {
  font-size: myrem(14);
  @include mq(md) {
    padding-bottom: myrem(5);
    border-bottom: 1px solid $purple1;
  }
}

.p-news__item:not(:first-child) {
  padding-top: myrem(3);
  @include mq(md) {
    padding-top: myrem(16);
  }
}

.p-news__item a {
  width: 100%;
  display: inline-block;
}

.p-news__head {
  display: inline-block;
}

.p-news__date {
  letter-spacing: myrem(0.5);
}

.p-news__body {
  padding-bottom: myrem(4);
  display: inline-block;
  border-bottom: 1px solid $purple1;
  letter-spacing: myrem(0.5);
  @include mq(md) {
    padding-left: myrem(10);
    padding-bottom: 0;
    border-bottom: none;
  }
}

.p-news__body-title {
  display: inline-block;
}

.p-news__body-text {
  padding-left: myrem(10);
  display: inline-block;
}

.p-news__more {
  padding-top: myrem(6);
  font-size: myrem(14);
  text-align: end;
  @include mq(md) {
    padding-top: myrem(18);
    font-size: myrem(16);
  }
}
