@use "global" as *;

.p-reservation {
  position: relative;
}

.p-reservation::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 14.3%;
  background-image: url(../images/bg1.jpg);
  background-size: cover;
  z-index: -10;
  @include mq(md) {
    top: 0;
    height: 13.5%;
  }
}

.p-reservation::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 14.3%;
  background-color: rgba(245, 240, 247, 0.5);
  background-size: cover;
  z-index: -10;
  @include mq(md) {
    top: 0;
    height: 13.5%;
  }
}

.p-reservation__wrapper {
  margin: 0 auto;
  padding-right: myrem(15);
  padding-left: myrem(15);
  max-width: myrem(520);
  width: 100%;
  @include mq(md) {
    padding-right: myrem(200);
    padding-left: myrem(200);
    max-width: myrem(1400);
  }
}

.p-reservation__inner {
  margin: 0 auto;
  padding: 0 myrem(15) 0;
  max-width: myrem(520);
  width: 100%;
  background-color: $white;
  border-radius: myrem(20);
  @include mq(md) {
    padding: 0 myrem(51) 0;
    max-width: myrem(1007);
  }
}

.p-reservation__title {
  padding-top: myrem(29);
  text-align: center;
  letter-spacing: myrem(1);
  @include mq(md) {
    padding-top: myrem(51);
    letter-spacing: myrem(3.3);
  }
}

.p-reservation__title::after {
  letter-spacing: myrem(0.5);
  @include mq(md) {
    letter-spacing: myrem(1.7);
  }
}

.p-reservation__form {
  margin-top: myrem(54);
  @include mq(md) {
    margin-top: myrem(27);
  }
}

.p-reservation label,
input,
textarea {
  @include mq(md) {
    cursor: pointer;
  }
}

.p-reservation input[type="email"],
.p-reservation input[type="text"],
.p-reservation textarea {
  box-sizing: border-box;
  border: 2px solid transparent;
}

.p-reservation input[type="email"]:focus,
.p-reservation input[type="text"]:focus,
.p-reservation textarea:focus {
  border: 2px solid $blue1;
  transition: all 0.3s;
  outline: none;
}

.p-reservation__item {
  padding: myrem(15) 0 myrem(23);
}

.p-reservation__item-mail {
  padding-bottom: 0;
}

.p-reservation__item-name {
  @include mq(md) {
    padding-top: myrem(28);
  }
}

.p-reservation__item-reading {
  @include mq(md) {
    padding-top: myrem(22);
  }
}

.p-reservation__item-gender {
  padding-top: myrem(39);
  @include mq(md) {
    padding-top: myrem(45);
  }
}

.p-reservation__item-consultation {
  @include mq(md) {
    padding-top: myrem(14);
  }
}

.p-reservation__item-message {
  padding-top: myrem(17);
}

.p-reservation__item-confirm {
  margin-top: myrem(-13);
  margin-left: myrem(29);
  padding: 0;
  letter-spacing: myrem(0.4);
  @include mq(md) {
    margin-top: myrem(23);
    margin-left: myrem(38);
    letter-spacing: myrem(1.3);
  }
}

// ラベル
.p-reservation__label {
  display: flex;
  align-items: center;
  letter-spacing: myrem(1.3);
}

// 必須
.p-reservation__required {
  margin-right: myrem(11);
  padding: myrem(5) myrem(10);
  font-size: myrem(12);
  font-weight: 500;
  color: $purple2;
  background-color: $pink;
}

.p-reservation__required-gray {
  background-color: $gray5;
}

// 回答
.p-reservation__answer {
  margin-top: myrem(20);
  padding: myrem(6) myrem(25);
  width: 100%;
  display: block;
  background-color: rgb(237, 245, 245, 0.6);
  border-radius: myrem(10);
  border: none;
  @include mq(md) {
    padding: myrem(16) myrem(25);
    border-radius: myrem(20);
  }
}

.p-reservation__answer-gender,
.p-reservation__answer-consultation {
  margin-top: myrem(17);
  margin-left: myrem(-6);
  padding: 0;
  width: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: unset;
  @include mq(md) {
    justify-content: flex-start;
    column-gap: myrem(30);
  }
}

.p-reservation__answer-consultation {
  justify-content: flex-start;
  column-gap: myrem(16);
  @include mq(md) {
    column-gap: myrem(16);
  }
}

.p-reservation__answer-content {
  margin-top: myrem(18);
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  column-gap: 4%;
  row-gap: myrem(10);
  background-color: unset;
  @include mq(md) {
    font-size: myrem(14);
    column-gap: 2.3%;
    row-gap: myrem(19);
  }
}

.p-reservation__answer-message {
  margin-top: myrem(15);
  height: myrem(182);
}

// 診察内容
.p-reservation__content-label {
  display: block;
  width: 48%;
  @include mq(md) {
    width: 23%;
  }
}

.p-reservation__item-content {
  display: none; /* デフォルトのinputは非表示 */
}

.p-reservation__content-input {
  padding: myrem(12) myrem(15);
  display: block;
  background-color: $gray4;
  border-radius: myrem(5);
  letter-spacing: myrem(0.1);
  transition: all 0.3s;
  @include mq(md) {
    padding: myrem(8) myrem(20) myrem(4);
    border-radius: myrem(8);
  }
}

.p-reservation__item-content:checked + span {
  color: #fff;
  background-color: $blue2;
  transition: all 0.3s;
  @include mq(md) {
    background-color: $blue1;
  }
}

// 注意事項
.p-reservation__caution {
  font-size: myrem(12);
  letter-spacing: myrem(1.2);
  line-height: 1.4;
  @include mq(md) {
    font-size: myrem(14);
  }
}

.p-reservation__caution-mail {
  margin-top: myrem(-8);
  @include mq(md) {
    margin-top: myrem(-4);
    letter-spacing: myrem(1.4);
  }
}

.p-reservation__caution-message {
  margin-top: myrem(11);
  letter-spacing: myrem(0.5);
  @include mq(md) {
    margin-top: myrem(20);
    letter-spacing: myrem(1.3);
  }
}

// ラジオボタン
.p-reservation__answer-item {
  display: flex;
  align-items: center;
  font-size: myrem(14);
}

.p-reservation__radio {
  position: relative;
  width: myrem(35);
  height: myrem(35);
  font-size: myrem(14);
  border: 1px solid #000;
  border-radius: 50%;
  vertical-align: myrem(-2);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  transition: border 0.25s linear;
  -webkit-transition: border 0.25s linear;
}

.p-reservation__radio:checked {
  border: 1px solid $blue2;
}

.p-reservation__radio:before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: myrem(20);
  height: myrem(20);
  border-radius: 50%;
  background-color: transparent;
  content: "";
  transition: background-color 0.25s linear;
  -webkit-transition: background-color 0.25s linear;
}

.p-reservation__radio:checked:before {
  background-color: $blue2;
  transition: background-color 0.25s linear;
  -webkit-transition: background-color 0.25s linear;
}

.p-reservation__radio-label {
  padding-left: myrem(6);
  @include mq(md) {
    padding-left: myrem(11);
  }
}

.p-reservation__radio-consultation-label {
  padding-left: myrem(9);
  @include mq(md) {
    padding-left: myrem(10);
  }
}

// 同意
.p-reservation__confirm-input {
  display: none;
}

.p-reservation__confirm-label {
  margin: 0 auto;
  width: fit-content;
  display: block;
  position: relative;
  font-size: myrem(12);
  @include mq(md) {
    font-size: myrem(14);
    text-align: center;
  }
}

.p-reservation__confirm-label::before {
  content: "";
  width: myrem(17);
  height: myrem(17);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: myrem(3);
  left: myrem(-29);
  background-color: #fff;
  border: 1px solid $black;
  @include mq(md) {
    top: myrem(5);
    left: myrem(-38);
  }
}

.p-reservation__confirm-input:checked + label:before {
  position: absolute;
  content: "\2713";
  color: $black;
  background-color: $white;
}

.p-reservation__confirm-underline {
  border-bottom: 1px solid #000;
}

// 送信ボタン
.p-reservation__form-send {
  margin: myrem(44) auto 0;
  width: myrem(240);
  @include mq(md) {
    margin-top: myrem(41);
  }
}
