@use "global" as *;

.p-thanks {
  position: relative;
  text-align: center;
}

.p-thanks::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 63%;
  background-image: url(../images/bg1.jpg);
  background-size: cover;
  z-index: -10;
  @include mq(md) {
    top: 0;
    height: 47%;
  }
}

.p-thanks::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 63%;
  background-color: rgba(245, 240, 247, 0.5);
  background-size: cover;
  z-index: -10;
  @include mq(md) {
    top: 0;
    height: 47%;
  }
}

.p-thanks__wrapper {
  margin: 0 auto;
  padding-right: myrem(15);
  padding-left: myrem(15);
  max-width: myrem(520);
  width: 100%;
  @include mq(md) {
    padding-right: myrem(200);
    padding-left: myrem(200);
    max-width: myrem(1400);
  }
}

.p-thanks__inner {
  margin: 0 auto;
  padding: 0 myrem(15) 0;
  max-width: myrem(520);
  width: 100%;
  background-color: $white;
  border-radius: myrem(20);
  @include mq(md) {
    padding: 0 myrem(51) 0;
    max-width: myrem(1007);
  }
}

.p-thanks__title {
  padding-top: myrem(45);
  position: relative;
  font-size: myrem(24);
  text-align: center;
  letter-spacing: myrem(2);
  line-height: 1.5;
  @include mq(md) {
    margin-left: myrem(-7);
    padding-top: myrem(67);
    font-size: myrem(32);
    letter-spacing: myrem(3.3);
  }
}

.p-thanks__title::before {
  content: "";
  width: myrem(55);
  height: myrem(4);
  position: absolute;
  bottom: myrem(-11);
  left: 50%;
  transform: translateX(-50%);
  background-color: $blue1;
  @include mq(md) {
    bottom: myrem(-19);
    left: 49.5%;
  }
}

.p-thanks__break::after {
  content: "\A";
  white-space: pre;
  @include mq(md) {
    content: "";
    white-space: normal;
  }
}

.p-thanks__break-both::after {
  content: "\A";
  white-space: pre;
}

.p-thanks__text {
  padding: myrem(26) 0 myrem(32);
  font-size: myrem(14);
  font-weight: 500;
  letter-spacing: myrem(0.5);
  line-height: 2.1;
  @include mq(md) {
    padding: myrem(48) 0 myrem(52);
    letter-spacing: myrem(1.4);
    line-height: 2.4;
  }
}

.p-thanks__button {
  margin: 0 auto;
  width: myrem(240);
}
