@use "global" as *;

.p-facility {
  position: relative;
}

.p-facility__title {
  text-align: center;
}

.p-facility__title::after {
  letter-spacing: myrem(0.7);
  @include mq(md) {
    letter-spacing: myrem(2);
  }
}

.p-facility__wrapper {
  margin: 0 auto;
  max-width: myrem(520);
  width: 100%;
  padding: myrem(11) myrem(15) myrem(78);
  position: relative;
  @include mq(md) {
    max-width: myrem(1400);
    padding-top: myrem(7);
    padding-right: myrem(200);
    padding-left: myrem(200);
  }
}

.p-facility__wrapper::before {
  content: "";
  display: block;
  position: absolute;
  top: -5.7%;
  left: 0;
  width: 68%;
  height: 105%;
  background-image: url(../images/bg2.jpg);
  background-size: cover;
  z-index: -10;
  @include mq(md) {
    top: -18%;
    width: 64%;
    height: 120.5%;
  }
}

.p-facility__wrapper::after {
  content: "";
  display: block;
  position: absolute;
  top: -5.7%;
  left: 0;
  width: 68%;
  height: 105%;
  background-color: rgba(237, 245, 245, 0.5);
  background-size: cover;
  z-index: -10;
  @include mq(md) {
    top: -18%;
    width: 64%;
    height: 120.5%;
  }
}

.p-facility__cards {
  margin-top: myrem(32);
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  row-gap: myrem(26);
  column-gap: myrem(30);
  @include mq(md) {
    margin-top: myrem(36);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 3%;
    row-gap: myrem(31);
  }
}

.p-facility__card {
  margin: 0 auto;
  @include mq(md) {
    margin: 0;
    width: 31%;
  }
}

.p-facility__card-image img {
  border-radius: myrem(15);
  aspect-ratio: 344/204;
  @include mq(md) {
    border-radius: myrem(20);
    aspect-ratio: 313/186;
  }
}

.p-facility__card-body {
  padding-top: myrem(19);
  padding-bottom: myrem(11);
  @include mq(md) {
    padding-top: myrem(18);
    padding-bottom: myrem(6);
  }
}

.p-facility__card-title {
  font-size: myrem(21);
  letter-spacing: myrem(3);
  @include mq(md) {
    font-size: myrem(20);
  }
}

.p-facility__card-text {
  margin-top: myrem(9);
  padding-right: myrem(3);
  letter-spacing: myrem(0.4);
  @include mq(md) {
    margin-top: myrem(6);
    padding-right: 0;
  }
}
