@use "global" as *;

.p-point {
  position: relative;
}

.p-point__wrapper {
  margin: 0 auto;
  padding-right: myrem(15);
  padding-left: myrem(15);
  max-width: myrem(520);
  width: 100%;
  position: relative;
  @include mq(md) {
    padding-right: myrem(200);
    padding-left: myrem(200);
    max-width: myrem(1400);
  }
}

.p-point__wrapper::before {
  content: "";
  display: block;
  position: absolute;
  top: -20%;
  left: -25%;
  width: 94%;
  height: 124.5%;
  background-image: url(../images/bg2.jpg);
  background-size: cover;
  z-index: -10;
  @include mq(md) {
    top: -28.5%;
    left: 0;
    width: 64%;
    height: 145%;
  }
}

.p-point__wrapper::after {
  content: "";
  display: block;
  position: absolute;
  top: -20%;
  left: -25%;
  width: 94%;
  height: 124.5%;
  background-color: rgba(237, 245, 245, 0.5);
  background-size: cover;
  z-index: -10;
  @include mq(md) {
    top: -28.5%;
    left: 0;
    width: 64%;
    height: 145%;
  }
}

.p-point__card {
  margin: 0 auto;
  @include mq(md) {
    max-width: myrem(1200);
    display: flex;
    align-items: center;
    column-gap: myrem(41);
  }
}

.p-point__card:not(:first-child) {
  margin-top: myrem(27);
  @include mq(md) {
    margin-top: myrem(38);
  }
}

.p-point__card-reverse {
  flex-direction: row-reverse;
}

.p-point__card-image {
  @include mq(md) {
    max-width: myrem(437);
    width: 100%;
  }
}

.p-point__card-image img {
  border-radius: myrem(10);
  aspect-ratio: 345/180;
  @include mq(md) {
    border-radius: myrem(20);
    aspect-ratio: 437/260;
  }
}

.p-point__card-body {
  padding-top: myrem(28);
  padding-right: myrem(15);
  max-width: myrem(522);
  width: 100%;
  @include mq(md) {
    padding-top: 0;
    padding-right: 0;
  }
}

.p-point__card-head {
  border-bottom: 1px solid $blue1;
  font-weight: 400;
}

.p-point__card-sub-title {
  width: fit-content;
  padding: 0 myrem(20);
  color: $white;
  background-color: $blue1;
  font-size: myrem(15);
  font-family: $font-sub2;
  @include mq(md) {
    font-size: myrem(16);
  }
}

.p-point__card-title {
  padding-bottom: myrem(7);
  font-size: myrem(19);
  @include mq(md) {
    padding-bottom: 0;
    font-size: myrem(24);
    letter-spacing: myrem(2.5);
  }
}

.p-point__card-title--heading {
  color: $blue1;
  font-size: myrem(33);
  letter-spacing: myrem(0.2);
  @include mq(md) {
    margin-right: myrem(3);
    font-size: myrem(38);
  }
}

.p-point__card-text {
  margin-top: myrem(23);
  padding-right: myrem(15);
  font-size: myrem(14);
  font-weight: 400;
  letter-spacing: myrem(0.5);
  @include mq(md) {
    margin-top: myrem(21);
    padding-right: 0;
    letter-spacing: myrem(0.4);
  }
}
