@use "global" as *;

.p-header {
  width: 100%;
  height: myrem(60);
  padding-left: myrem(20);
  background: $white;
  position: fixed;
  top: 0;
  z-index: 44;
  @include mq(md) {
    height: myrem(82);
    padding: 0;
    transition: 0.4s;
  }
}

.p-header__wrapper {
  height: inherit;
  display: flex;
  justify-content: space-between;

  @include mq(md) {
    align-items: center;
    gap: myrem(15);
    padding-left: myrem(50);
  }
}

.p-header__logo {
  display: block;
  position: relative;
  width: calc(190 / 375 * 100%);
  z-index: 44;
  @include mq(md) {
    width: calc(333 / 1400 * 100%);
    height: inherit;
    transition: 0.4s;
  }
}

.p-header__logo-image {
  @include mq(md) {
    height: inherit;
    transition: 0.4s;
  }
}

.p-header__logo-image img {
  aspect-ratio: 190/24;
  width: 100%;
  height: 100%;
  object-fit: contain;
  @include mq(md) {
    aspect-ratio: 333/40;
  }
}

.p-header__hamburger {
  height: inherit;
  width: myrem(50);
  padding: myrem(20) myrem(14);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: relative;
  z-index: 44;

  @include mq(md) {
    display: none;
  }
}

.p-header__hamburger span {
  width: 100%;
  height: 2px;
  background-color: $black;
  display: inline-block;
  border-radius: myrem(9);
  transform: translateY(0) rotate(0);
  transition: transform ease-in-out 0.3s;
}

.p-header__hamburger.is-active span:nth-child(1) {
  transform: translateY(myrem(6)) rotate(45deg);
}

.p-header__hamburger.is-active span:nth-child(2) {
  background-color: transparent;
}

.p-header__hamburger.is-active span:nth-child(3) {
  transform: translateY(myrem(-7)) rotate(-45deg);
}

.p-header__nav {
  display: none;
  position: fixed;
  height: 100vh;
  width: 100%;
  background-color: $white;
  top: 0;
  left: 0;
  padding: myrem(98) myrem(20) myrem(20);
  z-index: 43;
  @include mq(md) {
    display: flex;
    position: static;
    height: inherit;
    width: 100%;
    padding: 0;
    align-items: center;
    background-color: transparent;
    justify-content: flex-end;
  }
}

.p-header__nav-inner {
  height: 100%;
  overflow: auto;

  @include mq(md) {
    height: inherit;
    overflow: initial;
  }
}

.p-header__nav-inner2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: inherit;
  min-height: myrem(470);
  text-align: center;

  @include mq(md) {
    min-height: initial;
    height: inherit;
    flex-direction: row;
    justify-content: space-between;
    gap: myrem(15);
  }
}

.p-header__nav-list {
  width: myrem(312);
  margin-inline: auto;
  order: 2;
  margin: myrem(17) 0 0;
  @include mq(md) {
    order: initial;
    display: flex;
    width: initial;
    justify-content: flex-start;
    height: inherit;
    margin-inline: initial;
    margin-top: initial;
  }
}

.p-header__nav-item {
  @include mq(md) {
    height: inherit;
    position: relative;
    display: flex;
    align-items: center;
    transition: 0.4s;
  }
}

.p-header__nav-item:not(:first-child)::before {
  @include mq(md) {
    content: "";
    position: absolute;
    width: 1px;
    height: myrem(16);
    background: $black;
  }
}

.p-header__nav-item a {
  display: block;
  text-align: start;
  font-size: myrem(18);
  line-height: 1.5;
  letter-spacing: 0.05em;
  color: $black;
  width: 100%;

  @include mq(md) {
    padding: myrem(10);
    height: inherit;
    font-size: myrem(16);
    color: $black;
    display: flex;
    align-items: center;
    transition: 0.4s;
  }
}

.p-header__nav-accordion a:not(.p-header-dropMenu__item a) {
  pointer-events: none;
  @include mq(md) {
    pointer-events: painted;
  }
}

.p-header__nav-item a:not(.p-header-dropMenu__item a) {
  padding: myrem(15) myrem(9) myrem(8);
  border-bottom: 1px solid $blue2;
  @include mq(md) {
    padding: myrem(10);
    border-bottom: none;
  }
}

.p-header__nav-item a:hover {
  @include mq(md) {
    color: $blue1;
    font-weight: bold;
    opacity: 1;
  }
}

.p-header__nav-arrow {
  position: relative;
}

.p-header__nav-arrow::after {
  content: "";
  display: inline-block;
  width: myrem(15);
  height: myrem(15);
  position: absolute;
  top: myrem(15);
  right: myrem(12);
  border-top: 2px solid $blue1;
  border-right: 2px solid $blue1;
  transform: rotate(135deg);
  @include mq(md) {
    display: none;
  }
}

.p-header__nav-arrow.open::after {
  top: myrem(25);
  transform: rotate(-45deg);
  @include mq(md) {
    display: none;
  }
}

.p-header__nav-arrow a {
  @include mq(md) {
    display: flex;
  }
}

.p-header__dropMenu {
  display: none;
  @include mq(md) {
    transform: scaleY(0);
    transform-origin: top center;
    transition: 0.4s;
    background: rgb(255, 255, 255, 0.8);
    position: absolute;
    top: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 0 0 myrem(10) myrem(10);
  }
}

.p-header-dropMenu__item {
  padding: myrem(14) myrem(21) myrem(9);
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  @include mq(md) {
    padding: myrem(8);
    width: initial;
  }
}

.p-header-dropMenu__item::before {
  margin-right: myrem(9);
  content: "";
  width: myrem(16);
  height: myrem(1);
  background-color: $blue1;
  @include mq(md) {
    display: none;
  }
}

.p-header-dropMenu__item:not(:last-child):after {
  @include mq(md) {
    content: "";
    width: myrem(101);
    height: myrem(1);
    display: inline-block;
    background-color: rgb(72, 72, 72, 0.3);
    position: absolute;
    left: myrem(-9);
    margin-top: myrem(52);
  }
}

.p-header-dropMenu__item a {
  font-size: myrem(15);

  @include mq(md) {
    font-size: myrem(16);
    line-height: calc(34 / 16);
    letter-spacing: 0;
    padding-inline: 0;
    padding-block: myrem(3);
    transition: 0.4s;
  }
}

.p-header-dropMenu__item a:hover {
  @include mq(md) {
    color: $blue1;
    font-weight: bold;
    opacity: 1;
  }
}

.p-header__nav-item:hover .p-header__dropMenu {
  @include mq(md) {
    transform: scaleY(1);
  }
}

.p-header__btnList {
  display: flex;
  flex-direction: column-reverse;
  row-gap: myrem(12);
  width: myrem(315);
  @include mq(md) {
    width: initial;
    flex-direction: row;
    height: inherit;
    gap: initial;
  }
}

.p-header__btnItem {
  @include mq(md) {
    height: inherit;
    width: myrem(262);
  }
}

.p-header__btnItem--reservation {
  font-size: myrem(22);
  font-weight: 500;
  letter-spacing: myrem(2);

  @include mq(md) {
    width: myrem(120);
    height: inherit;
    font-size: myrem(18);
  }
}

.p-header__btnItem--reservation img {
  width: myrem(30);
  aspect-ratio: 30.43/31.97;
  @include mq(md) {
    display: none;
  }
}

/* ハンバーガーメニュー展開時、背景を固定 */
body.active {
  height: 100%;
  overflow: hidden;
}

.c-btn02 {
  background: $blue1;
  font-weight: bold;
  color: $white;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: myrem(16);
  width: 100%;
  border-radius: myrem(10);
  column-gap: myrem(13);

  @include mq(md) {
    height: inherit;
    border-radius: 0;
  }
}

.c-btn03 {
  padding: myrem(10) myrem(10) myrem(12);
  width: 100%;
  display: block;
  background-color: rgb(164, 221, 224, 0.3);
  border-radius: myrem(10);
  @include mq(md) {
    font-size: myrem(28);
    line-height: 1;
    background: $blue3;
    font-weight: bold;
    color: $black;
    display: inline-flex;
    align-items: flex-start;
    justify-content: center;
    padding: myrem(16) myrem(10);
    height: inherit;
    border-radius: 0;
    pointer-events: none;
    cursor: default;
    text-decoration: none;
  }
}

.c-btn03::before {
  @include mq(md) {
    content: "tel.";
    text-transform: uppercase;
    font-size: myrem(16);
    margin-top: myrem(10);
  }
}

.c-btn03 span {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  row-gap: myrem(6);
  font-size: myrem(26);
  font-weight: bold;
  line-height: 1;
  @include mq(md) {
    flex-direction: column;
    gap: myrem(5);
  }
}

.c-btn03 span::before {
  content: "";
  background-image: url(../images/tel.png);
  background-size: 52%;
  background-repeat: no-repeat;
  background-position: center;
  display: inline-block;
  width: 32px;
  height: 28px;
  @include mq(md) {
    display: none;
  }
}

.c-btn03 span::after {
  content: "お気軽にお問い合わせください";
  font-size: myrem(12);
  font-weight: 400;
  @include mq(md) {
    content: "営業時間　10:00〜19:00";
    margin-left: auto;
  }
}
