@use "global" as *;

.p-message {
  position: relative;
}

.p-message__wrapper {
  margin: 0 auto;
  padding-right: myrem(15);
  padding-left: myrem(15);
  max-width: myrem(520);
  width: 100%;
  @include mq(md) {
    padding-right: myrem(200);
    padding-left: myrem(200);
    max-width: myrem(1400);
  }
}

.p-message__container {
  display: flex;
  flex-direction: column;
  @include mq(md) {
    flex-direction: row;
    column-gap: myrem(40);
  }
}

.p-message__title-sp {
  text-align: center;
  letter-spacing: myrem(1);
  @include mq(md) {
    display: none;
  }
}

.p-message__title-sp::after {
  line-height: 0.5;
}

.p-message__title-pc {
  display: none;
  @include mq(md) {
    display: block;
  }
}

.p-message__title-pc::after {
  @include mq(md) {
    letter-spacing: myrem(2);
  }
}

.p-message__image {
  margin-top: myrem(33);
  margin-left: myrem(-15);
  max-width: myrem(360);
  width: 105%;
  @include mq(md) {
    margin-top: myrem(23);
    margin-left: myrem(-200);
    max-width: myrem(660);
    width: 331%;
  }
}

.p-message__image img {
  border-radius: 0 myrem(30) myrem(30) 0;
  aspect-ratio: 360/250;
  @include mq(md) {
    border-radius: 0 myrem(50) myrem(50) 0;
    aspect-ratio: 660/380;
  }
}

.p-message__box {
  position: relative;
  @include mq(md) {
    padding-top: myrem(95);
  }
}

.p-message__catch {
  position: absolute;
  top: myrem(-249);
  left: myrem(116);
  font-size: myrem(50);
  font-family: $font-sub;
  letter-spacing: myrem(0.1);
  color: $blue2;
  transform: rotate(-7deg);
  @include mq(md) {
    top: myrem(84);
    left: myrem(220);
    font-size: myrem(52);
    letter-spacing: myrem(0.1);
    white-space: nowrap;
  }
}

.p-message__text {
  padding: myrem(15) 0;
  font-size: myrem(14);
  letter-spacing: myrem(0.4);
  @include mq(md) {
    padding: myrem(38) 0;
  }
}
