@use "global" as *;

.p-access {
  position: relative;
}

.p-access::before {
  content: "";
  display: block;
  position: absolute;
  top: 25%;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(../images/bg1.jpg);
  background-size: cover;
  z-index: -10;
  @include mq(md) {
    top: 35%;
    height: 90%;
  }
}

.p-access::after {
  content: "";
  display: block;
  position: absolute;
  top: 25%;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(245, 240, 247, 0.5);
  background-size: cover;
  z-index: -10;
  @include mq(md) {
    top: 35%;
    height: 90%;
  }
}

.p-access__wrapper {
  max-width: myrem(520);
  width: 100%;
  margin: 0 auto;
  padding-right: myrem(15);
  padding-left: myrem(15);
  @include mq(md) {
    max-width: myrem(1400);
    padding-right: myrem(200);
    padding-bottom: myrem(41);
    padding-left: myrem(200);
  }
}

.p-access__title {
  text-align: center;
}

.p-access__title::after {
  letter-spacing: myrem(0.5);
  @include mq(md) {
    letter-spacing: myrem(2);
  }
}

.p-access iframe {
  margin-top: myrem(32);
  width: 100%;
  height: myrem(195);
  border-radius: myrem(30);
  @include mq(md) {
    margin-top: myrem(44);
    height: myrem(499);
  }
}

.p-access__box {
  margin-top: myrem(10);
  display: flex;
  flex-direction: column;
  row-gap: myrem(37);
  @include mq(md) {
    margin-top: myrem(48);
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    column-gap: myrem(112);
  }
}

.p-access__information-box {
  @include mq(md) {
    margin: 0 auto;
  }
}

.p-access__logo {
  font-size: myrem(24);
  font-weight: 100;
  letter-spacing: myrem(2.5);
  @include mq(md) {
    font-size: myrem(32);
    letter-spacing: myrem(3.3);
  }
}

.p-access__tel {
  margin-top: myrem(5);
  font-size: myrem(19);
  font-weight: bold;
  @include mq(md) {
    margin-top: myrem(13);
    font-size: myrem(22);
  }
}

.p-access__tel-number {
  margin-left: myrem(-3);
  font-size: myrem(30);
  letter-spacing: myrem(0.1);
  @include mq(md) {
    font-size: myrem(35);
  }
}

.p-access__address,
.p-access__time,
.p-access__close {
  font-size: myrem(12);
  @include mq(md) {
    font-size: myrem(14);
  }
}

.p-access__address {
  margin-top: myrem(7);
  letter-spacing: myrem(0.5);
  @include mq(md) {
    margin-top: myrem(14);
    letter-spacing: myrem(0.6);
  }
}

.p-access__time {
  margin-top: myrem(3);
  letter-spacing: myrem(0.7);
  @include mq(md) {
    margin-top: myrem(7);
  }
}

.p-access__close {
  margin-top: myrem(4);
  letter-spacing: myrem(0.8);
  @include mq(md) {
    margin-top: myrem(8);
  }
}

.p-access__schedule-box {
  width: 100%;
  font-size: myrem(14);
  @include mq(md) {
    margin: 0 auto;
    max-width: myrem(500);
    display: flex;
    align-items: end;
    font-size: myrem(16);
    line-height: 1.3;
  }
}

.p-access__schedule {
  display: block;
  border-collapse: collapse;
}

.p-access__schedule-title {
  padding: myrem(6) myrem(8) myrem(8);
  border-top: 1px solid $black;
  font-weight: 400;
  @include mq(md) {
    padding: myrem(6) myrem(18) myrem(8);
  }
}

.p-access__schedule-title-sun {
  padding: 0;
  @include mq(md) {
    padding: myrem(6) myrem(16) myrem(8);
  }
}

.p-access__schedule-title,
.p-access__schedule-text {
  text-align: center;
  border-bottom: 1px solid $black;
  white-space: nowrap;
}

.p-access__schedule-text {
  padding: myrem(7) 0 myrem(3);
  @include mq(md) {
    padding: myrem(17) 0 myrem(13);
  }
}

.p-access__schedule-hour {
  width: 13%;
  white-space: normal;
  @include mq(md) {
    padding: 0 myrem(13);
    width: auto;
    white-space: nowrap;
  }
}

.p-access__button {
  margin: myrem(39) auto 0;
  width: myrem(240);
  @include mq(md) {
    margin-top: myrem(48);
  }
}
