@use "global" as *;

.p-treatment {
  position: relative;
}

.p-treatment__wrapper {
  margin: 0 auto;
  padding-right: myrem(15);
  padding-left: myrem(15);
  max-width: myrem(520);
  width: 100%;
  @include mq(md) {
    padding-right: myrem(200);
    padding-left: myrem(200);
    max-width: myrem(1400);
  }
}

.p-treatment__wrapper::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 38%;
  background-image: url(../images/bg3.jpg);
  background-size: cover;
  z-index: -10;
  @include mq(md) {
    top: 0;
    height: 52%;
  }
}

.p-treatment__wrapper::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 38%;
  background-color: rgba(237, 245, 245, 0.5);
  background-size: cover;
  z-index: -10;
  @include mq(md) {
    top: 0;
    height: 52%;
  }
}

.p-treatment__title {
  text-align: center;
}

.p-treatment__title::after {
  letter-spacing: myrem(0.6);
  @include mq(md) {
    letter-spacing: myrem(1.8);
  }
}

.p-treatment__cards {
  margin-top: myrem(33);
  display: flex;
  flex-wrap: wrap;
  row-gap: myrem(27);
  column-gap: 2.5%;
  @include mq(md) {
    margin-top: myrem(47);
    flex-wrap: nowrap;
  }
}

.p-treatment__card {
  margin: 0 auto;
  @include mq(md) {
    max-width: myrem(230);
    display: flex;
    flex-direction: column;
  }
}

.p-treatment__card-image img {
  border-radius: myrem(15);
  aspect-ratio: 345/230;
  @include mq(md) {
    border-radius: myrem(30);
    aspect-ratio: 230/300;
  }
}

.p-treatment__card-body {
  padding-top: myrem(16);
  padding-bottom: myrem(11);
  @include mq(md) {
    padding-bottom: myrem(6);
  }
}

.p-treatment__card-title {
  font-size: myrem(22);
  text-align: center;
  letter-spacing: myrem(2.2);
}

.p-treatment__card-title1 {
  @include mq(md) {
    padding: 0 myrem(5);
  }
}

.p-treatment__card-title4 {
  @include mq(md) {
    padding: 0 myrem(16);
  }
}

.p-treatment__card-text {
  margin-top: myrem(7);
  padding-right: myrem(3);
  letter-spacing: myrem(0.4);
  @include mq(md) {
    padding-right: 0;
  }
}

.p-treatment__button {
  margin-top: auto;
  text-align: end;
}

.p-treatment__button-box {
  margin: myrem(39) auto 0;
  width: myrem(240);
  @include mq(md) {
    margin-top: myrem(59);
  }
}
